<template>
  <div id="app">
    <header>How to use</header>
    <div class="container">
      <div class="heading">Create Wallet</div>
      <div>
        <div class="input-container">
          <input type="text" placeholder="ERC20 Token address" v-model="tokenAddress"/>
          <button @click="createWallet">Submit</button>
        </div>
        <!-- <span class="help-txt">ERC20 Token address</span> -->
        <div class="output-container">
          <input type="text" placeholder="Memonic Word phrase" disabled v-model="memonic" />
        </div>
        <div class="output-container">
          <input type="text" placeholder="Private Key" disabled v-model="privateKey" />
        </div>
      </div>
    </div>

    <div class="container">
      <div class="heading">Load Wallet</div>
      <div>
        <div class="input-container">
          <input type="text" placeholder="Private key" />
          <button @click="loadWallet">LOAD</button>
        </div>
        <!-- <span class="help-txt">ERC20 Token address</span> -->
        <div class="output-container">
          <span class="success-msg" v-if="walletLoadSuccessful">SUCCESS</span>
        </div>
      </div>
    </div>

    <div class="container">
      <div class="heading">Send Amount</div>
      <div>
        <div class="input-container">
          <input type="text" placeholder="Amount to send"/>
          <input type="text" placeholder="Receiver Wallet Address"/>
          <button @click="sendAmount">SEND</button>
        </div>
        <!-- <span class="help-txt">ERC20 Token address</span> -->
        <div class="output-container">
          <span class="success-msg" v-if="amountSentSuccessful">SUCCESS</span>
        </div>
      </div>
    </div>

       <div class="container">
      <div class="heading">Display Wallet Address</div>
      <div>
        <div class="input-container">
          <input type="text" />
          <button @click="displayWallet">Submit</button>
        </div>
        <!-- <span class="help-txt">ERC20 Token address</span> -->
        <div class="output-container">
          <input type="text" placeholder="Wallet Address" disabled v-model="walletAddress"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import wallet from 'eth-wallet-light';
const entropy = 'hydro';
export default {
  name: 'app',
  data:()=>({
    amountSentSuccessful:false,
    walletLoadSuccessful:false,
    memonic:'',
    privateKey:'',
    walletAddress:'',
    keyStore:null,
    tokenAddress:''
  }),
  methods:{
    loadWallet(){
      this.walletLoadSuccessful = this.true;
    },
    sendAmount(){
      this.amountSentSuccessful = true;
    },
    createWallet(){
      async function createWallet(tokenAddress){
        let keyStore = await new wallet.Keystore()
        keyStore = await keyStore.initializeFromEntropy(entropy, tokenAddress);
        return keyStore;
      }
      createWallet(this.tokenAddress).then((res)=>{
        this.keyStore = res;
        this.memonic = this.keyStore.getMnemonic(this.tokenAddress);
        this.privateKey = this.keyStore.encodedPrivateKey.ciphertext;
      })
      
    },
    displayWallet(){
      this.walletAddress = this.keyStore.getAddress()
    }
  }
}
</script>

<style>
@import './App.css'
</style>
